<template>
  <div id="question">
    <head-top title="提问"></head-top>
    <div class="main" ref="main">
      <div class="questions" v-for="(item, i) in dataList" :key="i">
        <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        <div class="info">{{item['QContent']}}</div>
      </div>
    </div>
    <div class="footer">
      <input
        type="text"
        :class="content.length===0 ? 'long':'short'"
        v-model="content"
        placeholder="请输入提问内容"
        @keyup.enter="dataAdd"
      />
      <div class="footer_btn" v-if="content.length > 0">
        <van-button size="small" type="primary" @click="dataAdd">发送</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Button } from "vant";

export default {
  name: "",
  components: {
    [Button.name]: Button,
    headTop
  },
  props: {},
  data() {
    return {
      dataList: [],
      content: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.$gatewayHttp.question
        .getList({
          courseID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
            setTimeout(() => {
              this.$refs["main"].scrollTop = this.$refs["main"].scrollHeight;
            }, 200);
          }
        })
        .catch(err => {});
    },
    dataAdd() {
      this.$gatewayHttp.question
        .postDataSave({
          courseID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID,
          qcontent: this.content
        })
        .then(res => {
          if (res.Result) {
            this.dataList.push({
              CreateTime: "/Date(" + new Date().getTime() + ")/",
              QContent: this.content
            });
            this.content = "";
            setTimeout(() => {
              this.$refs["main"].scrollTop = this.$refs["main"].scrollHeight;
            }, 200);
            // this.getDataList();
          }
        })
        .catch(err => {});
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#question {
  width: 100%;
  height: 100%;

  .main {
    // overflow: hidden;
    height: calc(100vh - 4.5rem - 4rem);
    overflow-y: auto;

    .questions {
      width: 92vw;
      padding: 0.5rem 2vw;
      margin: 0 auto;
      margin-top: 1rem;

      text-align: left;
      background: rgba(255, 255, 255, 1);
      border-radius: 3px;

      word-break: break-all;
      word-wrap: break-word;
      .time {
        font-size: 1.2rem;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
        line-height: 2.5;
      }
      .info {
        padding: 0.5rem 0;
        font-size: 1.25rem;
        line-height: 1.6;
        color: rgba(51, 51, 51, 1);
      }
      &:last-child {
        margin-bottom: 1rem;
      }
    }
  }

  .footer {
    height: 4rem;
    background: #fff;

    input {
      float: left;
      height: 3rem;
      line-height: 3rem;
      margin: 0.5rem;
      padding-left: 0.5rem;

      background: rgba(235, 240, 239, 1);
      border-radius: 3px;
      // margin: 0 auto;
      text-align: left;
    }
    .long {
      width: calc(100vw - 1.5rem);
    }
    .short {
      width: calc(100vw - 1.5rem - 7.35rem);
    }

    .footer_btn {
      float: left;
      line-height: 3rem;
      padding: 0.5rem;
      .van-button {
        line-height: 3rem;
        height: 3rem;
        font-size: 1.3rem;
        // padding: 0 1.8vw;
      }
    }
  }
}
</style>


